import * as Brands from "../../pages/brands";

const brandsRoute ={
    path: '/brands',
    name: 'brands',
    component: Brands.Root,
    children:[
        {
            path:'',
            name: 'listBrands',
            component: Brands.List,
        },

        {
            path:'create',
            name: 'createBrands',
            component: Brands.Create,
        },

        {
            path:':id/edit',
            name: 'editBrands',
            component: Brands.Edit,
        }
    ]
}

export default brandsRoute



